import s from './Post.module.css';
import React from "react";

type PropsType={
    message:string
    like:number
}

const Post:React.FC<PropsType> = React.memo(props => {
        return (

            <div>
                <div className={s.item}>
                    <img
                        src='data:image/jpeg;base64,'></img>
                    {props.message}
                    <div><span>like {props.like}</span></div>
                </div>
            </div>
        );
    }
);


export default Post;
